.o-pagination {
  font-weight: 500;
  width: 100%;
  text-align: center;
  letter-spacing: 0;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;

  >span {
    display: inline-block;

    &.page {
      margin: 0 1.125rem;

      &:last-child {
        margin-right: 0;
      }

      &.current {
        cursor: default;
        color: $s-color--font-focus;
        border-bottom: 0.125rem solid $s-color--font-focus;
      }

      & a {
        display: inline-block;

        &:hover {
          color: $s-color--font-focus;
        }
      }
    }

    &.next,
    &.prev {
      font-size: 0;
      width: 1.25rem;
      height: 1.25rem;
      background: url('../../assets/icon-arrow.svg') no-repeat center;
      display: block;
      cursor: pointer;
      overflow: hidden;

      a {
        display: inline-block;
        width: 100%;
        height: 100%;
      }

      // Created using this: https://codepen.io/sosuke/pen/Pjoqqp
      &:hover {
        filter: invert(71%) sepia(12%) saturate(1577%) hue-rotate(326deg) brightness(95%) contrast(88%);
      }
    }

    &.next {
      margin-left: 0.5rem;
    }

    &.prev {
      transform: rotate(180deg);
      margin-right: 0.5rem;
    }
  }

  @include t-media-query($s-medium-up) {
    @include t-font--heading-3;

    >span {

      &.next,
      &.prev {
        width: 2rem;
        height: 2rem;
      }

      &.next {
        margin-left: 0.75rem;
      }

      &.prev {
        margin-right: 0.75rem;
      }


      &.page {
        color: $s-color--font;
        margin: 0 1.375rem;
      }
    }
  }
}