/*
 *  Blog Listing Page
 *
 *  Version:
 *    1.0.0 - 2021/09/20
 */

.c-blog-template {
  background-color: $s-color--background-subdued;
  text-align: center;
  margin: 0 auto;

  &__container {
    margin-left: auto;
    margin-right: auto;
    padding: 6.1875rem 1.25rem 5.5rem;
  }

  &__heading {
    margin-bottom: 1.6875rem;
  }

  &__title {
    margin-bottom: 2.0625rem;
  }

  &__description {
    margin: 0 auto;
    max-width: 35rem;
  }

  &__categories {
    max-width: 18.75rem;
    margin: 0 auto 3.75rem;

    .has-no-filters & {
      display: none;
    }
  }

  &__categories-title {
    margin-bottom: 1.625rem;
    opacity: 0.4;
  }

  &__categories-list {
    display: block;
    cursor: pointer;
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }

    &.is-selected {
      color: $s-color--font-focus;
    }
  }

  &__categories-list-label {
    margin-bottom: 0;
  }

  &__article-featured {
    margin-bottom: 5rem;
  }

  &__article-small {
    margin-bottom: 5.0625rem;
  }

  &__article-normal {
    margin-bottom: 5rem;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    &__container {
      padding-left: 2.5rem;
      padding-right: 2.5rem;
    }

    &__articles-featured {
      display: grid;
      grid-gap: 2.5rem;
      grid-template-columns: repeat(2, 1fr);
    }

    &__articles-wrapper {
      display: grid;
      grid-gap: 2.5rem;
      grid-template-columns: repeat(2, 1fr);
    }

    &__categories {
      grid-column: 1 / -1;
    }

    &__article-featured {
      grid-column: 2 / 1;
    }

    &__article-small {
      grid-column: 2 / 2;
    }
  }

  @include t-media-query($s-medium-up) {
    &__heading {
      margin-bottom: 4.375rem;
    }

    &__container {
      max-width: 125rem;
      padding: 9.125rem #{'min(6%, 7.5rem)'} 8.125rem;
    }

    &__title {
      @include t-font--heading-2-laptop;
      margin-bottom: 3.875rem;
    }

    &__description {
      line-height: 1.4375rem;
    }

    &__articles-featured {
      grid-template-columns: repeat(3, 1fr);

      .has-no-filters & {
        grid-template-columns: repeat(2, 1fr);
        gap: #{'min(9.5%, 11.875rem)'};
      }
    }

    &__categories {
      order: 2;
      grid-column: initial;
      margin-top: 7.3125rem;
    }

    &__article-featured {
      order: 1;
      grid-column: initial;
    }

    &__article-small {
      order: 3;
      grid-column: initial;
      margin-top: 8.0625rem;
    }

    &__articles-wrapper {
      margin-top: 5.9375rem;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__heading {
      margin-bottom: 5.625rem;
    }

    &__title {
      @include t-font--heading-2-desktop;
    }

    &__articles-featured {
      grid-template-columns: 40% 26% 28%;
    }

    &__article-small {
      margin-top: 9.3125rem;
    }

    &__categories {
      text-align: left;
      margin-top: 8.8125rem;
      padding-left: 0.75rem;
    }

    &__categories-list {
      margin-bottom: 0.625rem;
    }

    &__categories-title {
      margin-bottom: 1.75rem;
    }

    &__articles-wrapper {
      margin-top: 7.5rem;
    }

    &__article.is-featured {
      .o-article-thumbnail__image-container {
        padding-bottom: 81.83%;
      }

      .o-article-thumbnail__desc {
        max-width: 25.625rem;
        margin-top: 0.5625rem;
      }

      .o-article-thumbnail__title {
        margin-top: 2.125rem;
      }

      .o-article-thumbnail__info {
        margin-top: 0.0625rem;
      }
    }

    &__article.is-small {
      .o-article-thumbnail__image-container {
        padding-bottom: 115.78%;
      }

      .o-article-thumbnail__title {
        margin-top: 1.5625rem;
      }

      .o-article-thumbnail__desc {
        max-width: 21.25rem;
      }
    }

    &__article-normal {
      margin-bottom: 13.375rem;

      &:nth-child(3n+2) {
        transform: translateY(-5.125rem);
      }

      &:nth-last-child(-n+3) {
        margin-bottom: 6.3125rem;
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__articles-featured {
      grid-template-columns: 44.375rem 32.5rem 28.125rem;
    }

    &__article-small {
      margin-top: 13.3125rem;
    }

    &__categories {
      margin-top: 11.8125rem;
    }
  }
}
